body {
  font-family: 'Quicksand','Gowun Dodum', sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
  text-decoration:none;
}

 .detail-index{margin:30px auto;}
 .index{display:flex; justify-content:flex-start; padding:0;}
 .index li{font-size:17px; margin-right:10px; font-weight:500; opacity:0.6}
 .index li:nth-of-type(5){opacity:1}
 .dsld .swiper-slide {
  text-align: center; font-size: 18px; display: flex; justify-content: center;
  align-items: center;}
 .dsld .swiper-slide img {
  display: block; width: 350px; height: 400px; object-fit: cover;}
 .dtsld{width:85%;}
 .dtsld .swiper {
  width:100%; height: auto; margin-left:auto; margin-right: auto;}
 .dtsld .swiper-slide {
  background-size: cover; background-position: center; opacity:0.4;}
 .dtsld .swiper-slide-thumb-active {
 opacity: 1;}


 .textbox{text-align:left;}
 .textbox h4{font-size:30px; font-weight:500;}
 .textbox p{font-size:17px;}
 .textbox h6{font-size:20px; margin-top:4%;}
 .textbox .nav-link{color:#888;}
 .nav-item .active{font-weight:600;}
 .tab-pane ul li{list-style: square}
 .textbox .btn{background-color:#CCD5AE; border:none; color:#fff; }
 .textbox .btn:hover{background-color:#E9EDC9; color:#666;}



 .detailtap{padding-top:10%;}
 .detailtap .nav-link{font-size:18px; padding: 15px 30px; color:#888; border:none;}
 .detailtap .nav-link.active{border-bottom:2px solid #333;}

 
 .relative { margin:10% auto}
 .relative h3{font-size:35px;}
 .relative p{padding-top:15px;}
 .re ul{display:flex; flex-flow:row nowrap; justify-content:space-around; align-items:center;}
 .re ul li{margin:1%;background-color:#f7f7f7; text-align:center;}
 .re ul li:hover{box-shadow:1px 1px 25px #eee; cursor:pointer;}
 .re ul li img{width:80%;}



/*-모바일사이즈----------------------------------------------- */
@media screen and (max-width:390px){
  .textbox{text-align:center;}
  .textbox h4{font-size:23px; padding-top:30px}
  .textbox h6{font-size:18px;}
  .textbox .form-control-color{margin:0 auto;}
  .detailtap .nav-link{font-size:18px; padding:20px 50px;color:#888; border:none;}
  .detailtap .nav-link.active{border-bottom:2px solid #333;}
  .dsld .swiper-slide-active img{width:278px;}


  .relative { margin:20% auto}
  .relative h3{font-size:30px;}
  .relative p{padding-top:13px;}
  .re ul{display:flex; flex-flow:row wrap; justify-content:space-between; align-content:center;}
  .re ul li img{width:50%;}
}




/*-태블릿사이즈----------------------------------------------- */
@media screen and (min-width:391px) and (max-width:767px){
  .detailtap .nav-link{font-size:18px; padding: 15px; color:#888; border:none;}
  .detailtap .nav-link.active{border-bottom:2px solid #333;}
  .re ul{display:flex; flex-flow:column wrap; justify-content:space-between; align-content:center;}
}


