.carttop{background-image: url('../public/img/cartslide.jpg'); height:350px;background-size:cover; background-position:bottom; opacity:0.6}
.carttop h5{font-size:70px; padding-top:8%; color:#fff;}

.table tr{display:flex; flex-wrap: wrap; width:100%;}
.table tbody td{line-height:70px;}
.table th:nth-of-type(1){width:10%}
.table th:nth-of-type(2){width:15%;}
.table th:nth-of-type(3){width:30%;}
.table th:nth-of-type(4){width:10%;}
.table th:nth-of-type(5){width:25%;}
.table th:nth-of-type(6){width:10%;}

.table td:nth-of-type(1){width:10%}
.table td:nth-of-type(2){width:15%;}
.table td:nth-of-type(3){width:30%;}
.table td:nth-of-type(4){width:10%;}
.table td:nth-of-type(5){width:25%;}
.table td:nth-of-type(6){width:10%;}


.table tbody td .btn{background-color:#E9EDC9; border:none; margin:1% 5%;}
.table tbody td .btn:active{background-color:#CCD5AE;}
.table thead th:nth-of-type(2){text-align:left;}
.table tbody td:nth-of-type(3){text-align:left;}
.table tbody td:nth-of-type(6){font-weight:600;}
.table tfoot td{font-size:15px;line-height:50px; font-weight:bold;}



.cartbtn {background-color:#CCD5AE; border:none;}


/*-모바일사이즈----------------------------------------------- */
@media screen and (max-width:390px){
.carttop h5{font-size:38px; padding-top:38%; color:#fff;}

.table tbody td{line-height:50px;}
.table tfoot th{font-size:15px;line-height:25px;}
.table td .btn{margin:0.5%;}

.table th:nth-of-type(1){width:5%}
.table th:nth-of-type(2){width:20%;}
.table th:nth-of-type(3){display:none;width:0px;}
.table th:nth-of-type(4){width:20%;}
.table th:nth-of-type(5){width:35%;}
.table th:nth-of-type(6){width:20%;}

.table td:nth-of-type(1){width:5%}
.table td:nth-of-type(2){width:20%;}
.table td:nth-of-type(3){display:none;width:0px; overflow:hidden;}
.table td:nth-of-type(4){width:20%;}
.table td:nth-of-type(5){width:35%;}
.table td:nth-of-type(6){width:20%;}
}



/*-태블릿사이즈----------------------------------------------- */
@media screen and (min-width:391px) and (max-width:1200px){
.carttop h5{font-size:60px; padding-top:15%; color:#fff;}

.table td{line-height:50px;}
.table thead th:nth-of-type(2){text-align:left;}
.table td:nth-of-type(3){text-align:left; line-height:20px}
.table td:nth-of-type(6){font-weight:600;}
.table td .btn{width:10px; text-align:center;}
.table tfoot th{font-size:15px;line-height:30px;}

.table th:nth-of-type(1){width:5%}
.table th:nth-of-type(2){width:15%;}
.table th:nth-of-type(3){width:20%;}
.table th:nth-of-type(4){width:15%;}
.table th:nth-of-type(5){width:30%;}
.table th:nth-of-type(6){width:15%;}

.table td:nth-of-type(1){width:5%}
.table td:nth-of-type(2){width:15%;}
.table td:nth-of-type(3){width:20%;}
.table td:nth-of-type(4){width:15%;}
.table td:nth-of-type(5){width:30%;}
.table td:nth-of-type(6){width:15%;}
}


