.contacttop{background-image: url('../public/img/contact.jpg'); height:700px;background-size:cover; background-position:center; font-size:20px;}
.contacttop h2{font-size:70px; padding:3% 0;}

.contactbox{background-color:rgba(255,255,255,0.3); padding:5% 0}


.contactbox .right p{font-size:15px;}

.contactbox .contactbtn{background-color:#CCD5AE; border:none; margin:1%;}
.contactbox .contactbtn:hover{background-color:#E9EDC9; color:#666;}






/*-모바일사이즈----------------------------------------------- */
@media screen and (max-width:390px){
    .contacttop h2{font-size:45px; padding:10% 0;}
    .contactbox {padding:0;}
}

