.des{display:flex; flex-flow: row nowrap; justify-content:space-around; padding:80px 0; width:100%;}
.des-left{display:flex; flex-flow:column wrap; justify-content:center; text-align:left; width:45%; object-fit: cover;}
.des-left h3{font-size:32px; padding-bottom:5%;}
.des-left p{width:100%; font-size:18px;}
.des-left li{font-size:18px; list-style:square; margin:2% 0}


.additional h3{text-align:left; padding-top:7%; font-size:22px;}
.additional table {text-align:left; padding:0; margin:5% 0;}
.additional table tr{line-height:40px;}
.additional table td{font-size:15px; padding:0 20px; color:#666;}


.review h3{text-align:left; padding:7% 0 3%; font-size:22px;}
.review .reviewbox{display:flex; flex-flow:row nowrap; justify-content:space-between;}
.review .reviewbox .reviewleft{width:20%; text-align:left;}
.reviewleft h5{font-size:18px;}
.reviewleft h6{color:#888; font-size:15px;}
.review .reviewbox .reviewright{width:80%; text-align:left;}
.reviewright h5{font-weight:bold; font-size:17px;}
.reviewright p{width:90%; font-size:15px;}
.reviewright span{margin-right:3%;}



/*-모바일사이즈----------------------------------------------- */
@media screen and (max-width:390px){

    .des{display:flex; flex-flow: row wrap; justify-content:space-around; padding:50px 0; width:100%;}
    .des-left{display:flex; flex-flow:column wrap; justify-content:center; text-align:left; width:85%; object-fit: cover;}
    .des-left h3{font-size:25px; padding:5% 0;}
    .des-left p{font-size:14px;}
    .des-left li{font-size:14px; list-style:square; margin:2% 0}

    
    .additional h3{text-align:center;padding-top:15%; font-size:20px;}
    .additional table {text-align:center; margin:10% auto;}
    .additional table td{font-size:14px; color:#666;}

    
    
.review h3{text-align:center; padding:10%; font-size:22px;}
.review .reviewbox{display:flex; flex-flow:row nowrap; justify-content:space-around;}
.review .reviewbox .reviewleft{width:30%; text-align:left;}
.reviewleft h5{font-size:16px;}
.reviewleft h6{color:#888; font-size:13px;}
.review .reviewbox .reviewright{width:70%; text-align:left;}
.reviewright h5{font-weight:bold; font-size:16px;}
.reviewright p{width:90%; font-size:12px;}
.reviewright span{margin-right:3%; font-size:12px;}
}

/*-태블릿사이즈----------------------------------------------- */
@media screen and (min-width:391px) and (max-width:767px){
.des{display:flex; flex-flow: row wrap; justify-content:space-between; padding:80px 0; width:100%;}
.des-left{display:flex; flex-flow:column wrap; justify-content:center; text-align:left; width:90%; object-fit: cover;}
.des-left h3{font-size:32px; padding:5% 0;}
.des-left p{width:100%; font-size:18px;}
.des-left li{font-size:18px; list-style:square; margin:2% 0}
.des-right img{margin:0 auto;}
}