@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&family=Quicksand:wght@300;400;500;600;700&display=swap');


ul,ol,a{text-decoration:none;}

.App {
  text-align: center;
  font-family: 'Quicksand','Gowun Dodum', sans-serif;}

.topnav{background-color:#CCD5AE; height:40px;}
.topnav_text{font-size:13px;}
.me-auto{margin:0 auto; font-weight:600;}
.justify-content-end{font-size:13px;}
.navbar-brand{cursor:pointer;}



.footer_container{background-color:#E9EDC9;padding:20px 0; overflow:hidden;}
.footer ul,li{list-style: none; text-align:left;}
.footer .ul{font-size:18px; font-weight:600; margin:10px 0; position:relative;}
.footer .ul:after{content:""; width:32px; height:3px; background-color:#CCD5AE; position:absolute; left:0; bottom:-5px;}
.footer li{line-height:30px; cursor:pointer;}
.footer li:hover{font-weight:600;}





/*-모바일사이즈----------------------------------------------- */
@media screen and (max-width:390px){
  .topnav{font-size:9px;}
  .topnav .container{overflow:hidden;}
}
  
  /*-태블릿사이즈----------------------------------------------- */
  @media screen and (min-width:391px) and (max-width:767px){
  .topnav{font-size:10px;}
  .topnav .container{overflow:hidden;}
  }
  
  
